export default {
	data() {
		return {
			list:[]
		}
	},
	onLoad() {
		this.getData()
	},
	methods: {
		Issue(){
			uni.navigateTo({
				url:'../../pagesThree/PayInvoice/PayInvoice'
			})
		},
		goDown(){
			uni.navigateTo({
				url:'../../pagesMy/DownLoad/DownLoad'
			})
		},
		getData(){
			this.$api.pay_record({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				
			}).then(res=>{
				this.list = res.data.data
			})
		}
	}
}
